@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
hr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}


.container-box {
  
  width: 80%;
  background-color: transparent;
  position: relative;
  z-index: 9;
}
:root {
  --font-color: #424242;
  --bg-color: #fff;
}

[data-theme="dark"] {
  --font-color: #fff;
  --bg-color: #161625;
}

/*slider switch css */

.theme-switch {
  display: inline-block;
  height: 34px;
  top:2rem;
  position: relative;
  width: 60px;
}

.theme-switch input {
  display:none;
}



