.focus {
    position:relative;
  top: 0%;
  left: 0;;
  right:0;
  bottom: 0;
    
    transition: -webkit-transform 0.3s;
    
    transition: transform 0.3s;
    
    transition: transform 0.3s, -webkit-transform 0.3s;
  }
 
  .focus:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  
  .card-title {
    font-size: 2em;
    font-weight: 700;
  }
  
  .image-style {
    width: 3em;
  }
  
  .font-details {
    font-family:"Roboto Condensed", sans-serif;

    font-size: 3em;
    font-weight: 500;
